*{
  margin:0;
  padding:0;
  box-sizing: border-box
}
.App{
display: flex;
flex-direction: column;
align-items: center;
background: linear-gradient(45deg, #606c88 0%,#3f4c6b 100%);
min-height: 100vh;
max-height: 100%;
}
.heading{
  width:80%;
  height:60px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  border-radius: 10px;
}